.row{
    margin-left: 20px;
    margin-bottom: 20px;
}
.movie_row{
    display: flex;
    overflow-x: scroll;
    margin-top: 20px;
}
::-webkit-scrollbar{
    display: none;
}

.movies{
    height: 200px;
    width: 100%;
    margin-right: 20px;
    transition: 500ms;
    cursor: pointer;
}
.movies:hover{
    transform: scale(1.06);
}